export const userAuth = (isAuthenticated, navigate) => {
  if (isAuthenticated !== undefined) {
    setTimeout(() => {
      if (isAuthenticated === false) {
        console.log("userAuth", isAuthenticated);
        navigate("/login");
      }
    }, 500);
  }
};

export const adminAuth = (admin, user, navigate, alert) => {
  if (
    admin === true &&
    user &&
    user.role !== undefined &&
    user.role !== "admin"
  ) {
    console.log("adminAuth");
    navigate("/login");
    alert.error("Cannot Access Admin Authorities");
  }
};
